
var numberValidationMethod = {

numberValidation:function (val)
{		

	console.log( "Enter number validation numberValidationMethod - "+ val);

		var retVal = true;
		var reg = new RegExp('^[0-9]+$');
		if(val != '')
		{
			if (!reg.test(val)) 
			{
				retVal=false;
			}
		}
		else
		{
			retVal = false; // empty value is not a number
		}
	return retVal
}

}
module.exports = numberValidationMethod;